import "./carousel/slick";
$(document).ready(function(){
	$('.carousel__inner').slick({
		centerMode: true,
		centerPadding: '60px',
		slidesToShow: 1,
		arrows: true,
		autoplay: true,
		variableWidth: true,
		infinite: true,
		draggable: false,
		cssEase: 'linear',
		prevArrow: '<button type="button" class="slick-prev"><img src="img/icons/carousel/arrow-left.svg"></button>',
		nextArrow: '<button type="button" class="slick-next"><img src="img/icons/carousel/arrow-right.svg"></button>',
		responsive: [
		  {
			breakpoint: 768,
			settings: {
			  arrows: false,
			  centerMode: true,
			  centerPadding: '40px',
			  slidesToShow: 1,
			  autoplay: true,
			}
		  },
		  {
			breakpoint: 480,
			settings: {
			  arrows: false,
			  centerMode: true,
			  centerPadding: '40px',
			  slidesToShow: 1,
			  autoplay: true,
			}
		  }
		]
	  });
});